@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;600&family=Quicksand:wght@300;400;500;600&display=swap");

/* ======= VARIABLE CSS ============ */

:root {
  --header-height: 3rem;

  /* ====== COLORS ====== */
  --hue: 0;
  --sat: 0%;
  --title-color: #333;
  --title-color-dark: #000;
  --text-color: #969696;
  --body-color: #fafafa;
  --container-color: #fff;
  /* ====== Font And Typography ====== */
  --body-font: "Poppins", sans-serif;

  /*  .5rem = 8px | 1rem = 16px */
  --lg-font-size: 3.5rem;
  --h1: 2.25rem;
  --h2: 1.5rem;
  --h3: 1.25rem;
  --normal: 1rem;
  --font-sm: 0.875rem;
  --font-xs: 0.813rem;
  --font-2xs: 0.625rem;

  /* ====== Font Weight ====== */
  --font-normal: 400;
  --font-medium: 500;
  --font-semibold: 600;

  /* ====== Margin Bottom ====== */
  /*  .5rem = 8px | 1rem = 16px */
  --mb-0-25: 0.25rem;
  --mb-0-5: 0.5rem;
  --mb-0-75: 0.75rem;
  --mb-1: 1rem;
  --mb-1-5: 1.5rem;
  --mb-2: 2rem;
  --mb-2-5: 2.5rem;
  --mb-3: 3rem;

  /* ====== Z INDEX ====== */
  --z-tooltip: 10;
  --z-fixed: 100;
  --z-modal: 1000;
}

/* Responsive Typography */
@media screen and (max-width: 992px) {
  :root {
    --lg-font-size: 2.75rem;
    --h1: 1.5rem;
    --h2: 1.25rem;
    --h3: 1rem;
    --normal: 0.938rem;
    --font-sm: 0.813rem;
    --font-xs: 0.75rem;
  }
}

/* ====== BASE ====== */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

body,
button,
input,
textarea {
  font-family: var(--body-font);
  font-size: var(--font-normal);
}

body {
  background-color: var(--body-color);
  color: var(--text-color);
}

h1,
h2,
h3 {
  color: var(--title-color);
  font-weight: var(--font-semibold);
}

ul {
  list-style: none;
}

a {
  text-decoration: none;
}

button {
  cursor: pointer;
  border: none;
  outline: none;
}

img {
  max-width: 100%;
}
/* ====== REUSABLE CSS CLASSES ====== */
.section {
  padding: 6rem 0rem 2rem;
}

.section__title {
  font-size: var(--h1);
  color: var(--title-color);
}

.section__subtitle {
  display: block;
  font-size: var(--font-sm);
  margin-bottom: 4rem;
}

.section__title,
.section__subtitle {
  text-align: center;
}

/* ====== LAYOUT ====== */
.container {
  max-width: 968px;
  margin-left: auto;
  margin-right: auto;
}

.grid {
  display: grid;
  gap: 1.5rem;
}
/* ====== BUTTONS ====== */
.button {
  display: inline-block;
  background-color: var(--title-color);
  color: var(--container-color);
  padding: 1.25rem 2rem;
  border-radius: 1rem;
  font-weight: var(--font-medium);
}

.button:hover {
  background-color: var(--title-color-dark);
}

.button__icon {
  margin-left: var(--mb-0-5);
}

.button--flex {
  display: inline-flex;
  align-items: center;
}
/* ====== BREAKPOINTS ====== */
/* ====== LG DEVICES ====== */
@media screen and (max-width: 992px) {
  .container {
    margin-left: var(--mb-1-5);
    margin-right: var(--mb-1-5);
  }

  .button {
    padding: 1rem 1.75rem;
  }

  .button__icon {
    width: 22px;
    height: 22px;
  }
}

/* ====== MD DEVICES ====== */
@media screen and (max-width: 768px) {
  body {
    margin: 0 0 var(--header-height) 0;
  }
  .section {
    padding: 2rem 0rem 4rem;
  }

  .section__subtitle {
    margin-bottom: var(--mb-3);
  }
}

/* ====== SM DEVICES ====== */
@media screen and (max-width: 350px) {
  :root {
    --lg-font-size: 2.25rem;
  }

  .container {
    margin-left: var(--mb-1-5);
    margin-right: var(--mb-1-5);
  }
}
